import React, {
  useCallback, useEffect, useMemo, useState
} from 'react';
import 'react-phone-number-input/style.css';
import {
  Box, Button, FormLabel
} from '@chakra-ui/react';
import { useTranslation } from 'react-i18next';
import { shallowEqual } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import MiniBox from '../../../layout/MiniBox';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import { getPerson, register } from '../../../store/middlewares/user';
import { apiErrorHandler, findNextScreen } from '../../../utils/general';
import SsnInput from '../../../components/SsnInput';
import { fetchProgramConfig } from '../../../store/middlewares/core';

const Ssn = () => {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const navigate = useNavigate();
  const [ssn, setValue] = useState<any>('');

  const { otp, phone } = useAppSelector((state) => state.user.userDetails, shallowEqual);

  useEffect(() => {
    if (!phone || !otp) {
      navigate('/auth/mobile');
    }
  }, [otp, phone]);

  const isValid = useMemo(() => {
    if (ssn && ssn.length === 4) {
      return true;
    }
    return false;
  }, [ssn]);

  const submit = useCallback(() => {
    if (isValid && otp) {
      dispatch(register(ssn))
        .then(() => {
          dispatch(getPerson()).then((personDetails) => {
            dispatch(fetchProgramConfig(personDetails.programId));
            /**
             * find next screen by kyc details
             */
            findNextScreen(personDetails.kyc)
              .then((nextScreen) => {
                navigate(nextScreen);
              });
          });
        }).catch(apiErrorHandler);
    }
  }, [isValid, otp, ssn]);

  return (
    <div className="page">
      <form onSubmit={(e) => {
        e.preventDefault();
        submit();
      }}
      >
        <MiniBox
          varient="box"
          heading={t('ssn.title')}
          footer={(
            <Button width="100%" variant="primary" disabled={!isValid} onClick={submit}>
              {t('ssn.ctaText')}
            </Button>
  )}
        >
          <Box width="100%" paddingTop="15px" paddingBottom="30px">
            <FormLabel variant="primary">
              {t('ssn.ssnLabel')}
            </FormLabel>
            <SsnInput onChange={setValue} value={ssn} />
          </Box>
        </MiniBox>
      </form>
    </div>
  );
};

export default Ssn;
