import React, { useLayoutEffect, useMemo, useRef } from 'react';
import { Input } from '@chakra-ui/react';
import Inputmask from 'inputmask';
import { documentTypes } from '../utils/general';

type Props = {
    isLast4?: boolean;
    idType?: string;
    value: string;
    // eslint-disable-next-line no-unused-vars
    onChange: (maskedValue: string, unmaskedValue: string, isValid: boolean) => void;
}

const SsnInput:React.FC<Props> = ({
  onChange, value, isLast4, idType = 'ssn'
}) => {
  const mask = useMemo(() => {
    const doc = documentTypes.find((d) => d.type === idType);
    if (!doc) {
      return isLast4 ? '9999' : '999-99-9999';
    }
    return isLast4 ? doc.last4 : doc.full;
  }, [isLast4, documentTypes, idType]);

  const input = useRef(null);

  useLayoutEffect(() => {
    if (input.current) {
      Inputmask({
        mask,
        showMaskOnHover: false,
        showMaskOnFocus: false,
        jitMasking: true,
      }).mask(input.current);
    }
  }, [mask]);

  return (
  // eslint-disable-next-line react/jsx-props-no-spreading
    <Input
      data-testid="ssn-input"
      variant="primary"
      ref={input}
      onChange={(e:any) => {
        const v = isLast4 ? e.target.value.substring(0, 4) : e.target.value;
        const isValid = Inputmask.isValid(v, { mask });
        const unmaskedValue: string = (input.current as any)?.inputmask.unmaskedvalue();
        onChange(v, unmaskedValue, isValid);
      }}
      value={value}
    />
  );
};

SsnInput.defaultProps = {
  isLast4: true,
};

export default SsnInput;
